@import url(https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css);
body {
  margin: 0;
  padding: 0;
}

.nav {  
  position: fixed;
  cursor: pointer;
  top : 0;
  width: 100%;
  border-bottom: 1.5px solid #000;
  text-align: center;
  margin: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #000;
  z-index: 999;
  color:#FFF;
}

.footer {
  position: fixed;
  cursor: pointer;
  bottom : 0;
  height: 50px;
  width: 100%;
  border-top: 1px solid rgb(151, 151, 151);
  text-align: center;
  margin: auto;
  padding-top: 5px;
  background: rgba(255,255,255,0.8);
  z-index: 999;
  color:#000;
}

.footer-menu {
  display: inline-block;
  width: 20%;
  margin: auto;
  background-color : rgba( 255, 255, 255, 0.5 );
}
.footer-icon {
  display: inline-block;
}
.footer-icon img {
  display: block;
}

.footer-logo {
  width: 20px;
}

.footer-name {
  color : rgb(150, 150, 150);
  font-size: 7pt;
  text-align: center;
}

.logo {
  vertical-align: middle;
  width: 30px;
}

.head {
  font-size: 15px;
}

.carousel {
  border-bottom: 1.5px solid #000;
}

.title {
  padding-left: 5px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  border-left: 3px solid #000;
}

.blank{
  padding:28px;
}

.item > img {
  height: 300px !important;
  width: 100%;
}

.item-active > img {
  height: 300px !important;
  width: 100%;
}

table{
  width:100%;
  text-align: center;
  border-collapse: collapse;
}

thead{
  border-bottom: 1px solid #000;
  background-color: #f9f9f9;
}

th{
  text-align: center;
}

td{
  text-align: center;
}


.table-responsive {
  width: 90%;
  text-align: center;
  border-collapse: collapse;
  margin-left: 5%;
  margin-right: 5%;
}

.notice-title {
  text-align: left;
}

.study-state {
  text-align: right;
}

.notice-title-active {
  background: #cacaca;
  text-align: left;
}

.notice-author {
}

.notice-author-active {
  background: #cacaca;
}

.content {
}

.table-responsive {
  border : 0px;
}

.table>thead>tr>th {
  border-bottom: #fff !important;
}

table {
  border-color: #fff !important;
}

thead {
  border-top:0px;
}

th {
  background-color: #fff;
}

tr {
  border-bottom: 1px dotted #000;
}


.pagination {
    position: absolute; 
    left: 50%; 
    -webkit-transform: translateX(-50%); 
            transform: translateX(-50%); 
    margin: 0;
    margin-bottom: 30px;
}

#page-wrap {
  overflow: auto;
}

.menu-item {
  cursor: pointer;
}

.bm-item {
  display: inline-block;
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: fixed;
  width: 25px;
  height: 25px;
  right: 15px;
  bottom: 9px;
}

.bm-burger-bars {
  background: #373a47;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  cursor: pointer;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
.login {
    border: 1px solid rgb(112, 112, 112);
    border-radius: 20px;
    margin: 8px;
    padding: 8px;
}
